.UI-container {
  display: flex;
  flex-wrap: wrap;
  width: 90%;
  height: 98%;
  margin: 0 auto;
  overflow: hidden;

  .image-container {
    overflow: hidden;
    width: 30em;
    height: 18em;
    margin: 1em;
    border-radius: 5px;
    box-shadow: 0px 2px 8px rgba(0, 0, 0, 0.3);
  }

  .image {
    width: 100%;
    height: 100%;
    object-fit: cover;
    cursor: pointer;
    transition: all 0.2s ease-out;
  }

  .image:hover {
    transform: scale(1.05);
  }
}
