@import "../../global.scss";

.graphic-design-container {
  display: flex;
  flex-wrap: wrap;
  width: 90%;
  height: 98%;
  margin: 0 auto;
  padding: 0 5em;
  align-items: center;

  .image-container {
    overflow: hidden;
    width: 25em;
    aspect-ratio: 4 / 3;
    margin: 0.8em;
    border-radius: 5px;
    box-shadow: 0px 2px 8px rgba(0, 0, 0, 0.3);

    @include laptop {
      width: 16em;
    }

    @include mobile {
      margin: 0.5em;
      width: 10em;
    }
  }

  .image {
    width: 100%;
    height: 100%;
    object-fit: cover;
    cursor: pointer;
    transition: all 0.2s ease-out;
  }

  .image:hover {
    transform: scale(1.05);
  }
}
